    <!-- ÂÜÖÂÆπÂ±Ç -->
    <view class="content-wrapper">

    <!-- Logo ÂíåÊ†áÈ¢ò -->
    <view class="logo-section">
      <image class="logo" src="/static/zhuye/yellow_love.png" mode="aspectFit" />
      <text class="app-name">ÁîúËúúÊó∂ÂÖâ</text>
      <text class="app-slogan">ËÆ∞ÂΩïÊØè‰∏Ä‰∏™Áà±ÁöÑÁû¨Èó¥</text>
    </view>

    <!-- ÁôªÂΩïÂå∫ÂüüÔºàÊó†Âç°ÁâáÔºâ -->
    <view class="login-area">
      <!-- Áî®Êà∑‰ø°ÊÅØÈ¢ÑËßàÔºàÊéàÊùÉÂêéÊòæÁ§∫Ôºâ -->
      <view v-if="userInfo.nickName" class="user-preview">
        <image class="avatar" :src="userInfo.avatarUrl" mode="aspectFill" />
        <text class="nickname">{{ userInfo.nickName }}</text>
      </view>

      <!-- ÁôªÂΩïÊåâÈíÆ -->
      <button 
        v-if="!isLoggedIn" 
        class="login-btn" 
        @click="handleWxLogin"
        :loading="isLoading"
      >
        <text class="btn-icon">üì±</text>
        <text class="btn-text">ÂæÆ‰ø°ÊéàÊùÉÁôªÂΩï</text>
      </button>

      <!-- Ê∏∏ÂÆ¢ÁôªÂΩïÊåâÈíÆ -->
      <button 
        v-if="!isLoggedIn" 
        class="guest-btn" 
        @click="handleGuestLogin"
      >
        <text class="btn-icon">üë§</text>
        <text class="btn-text">Ê∏∏ÂÆ¢ÁôªÂΩï</text>
      </button>




      <!-- ÊèêÁ§∫‰ø°ÊÅØ -->
      <view class="tips">
        <text class="tip-text">ÁôªÂΩïÂç≥Ë°®Á§∫ÂêåÊÑè</text>
        <text class="link-text">„ÄäÁî®Êà∑ÂçèËÆÆ„Äã</text>
        <text class="tip-text">Âíå</text>
        <text class="link-text">„ÄäÈöêÁßÅÊîøÁ≠ñ„Äã</text>
      </view>
    </view>
    </view>
  </view>
</template>

<script>
import http from '@/utils/http.js';
import config from '@/utils/config.js';

export default {
  data() {
    return {
      isLoggedIn: false,
      isLoading: false,
      userInfo: {
        nickName: '',
        avatarUrl: ''
      }
    };
  },
  onLoad() {
    // Ê£ÄÊü•ÊòØÂê¶Â∑≤ÁôªÂΩï
    this.checkLoginStatus();
  },
  methods: {
    // Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
    checkLoginStatus() {
      try {
        const loginInfo = uni.getStorageSync('login_info');
        if (loginInfo && loginInfo.isLoggedIn) {
          console.log('Ê£ÄÊµãÂà∞Â∑≤ÁôªÂΩïÔºåËá™Âä®Ë∑≥ËΩ¨Âà∞È¶ñÈ°µ');
          this.isLoggedIn = true;
          this.userInfo = loginInfo.userInfo || {};
          // ‚úÖ Á´ãÂç≥Ë∑≥ËΩ¨Âà∞È¶ñÈ°µÔºå‰∏çÊòæÁ§∫ÁôªÂΩïÈ°µÈù¢
          setTimeout(() => {
            uni.reLaunch({
              url: '/pages/index/index'
            });
          }, 300);
        }
      } catch (e) {
        console.error('Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅÂ§±Ë¥•', e);
      }
    },

    /**
     * Ê∏∏ÂÆ¢ÁôªÂΩïÂ§ÑÁêÜ
     * Êó†ÈúÄÊéàÊùÉÔºåÁõ¥Êé•‰ΩøÁî®ÈªòËÆ§Áî®Êà∑‰ø°ÊÅØËøõÂÖ•Â∫îÁî®
     */
    handleGuestLogin() {
      // ÂàõÂª∫Ê∏∏ÂÆ¢Áî®Êà∑‰ø°ÊÅØ
      const guestUserInfo = {
        nickName: 'Ê∏∏ÂÆ¢Áî®Êà∑',
        avatarUrl: '/static/zhuye/smile.png',
        displayName: 'Ê∏∏ÂÆ¢Áî®Êà∑',
        displayAvatar: '/static/zhuye/smile.png',
        isGuest: true
      };

      // ‰øùÂ≠òÊ∏∏ÂÆ¢ÁôªÂΩï‰ø°ÊÅØ
      const loginInfo = {
        isLoggedIn: true,
        userInfo: guestUserInfo,
        isGuest: true, // Ê†áËÆ∞‰∏∫Ê∏∏ÂÆ¢ÁôªÂΩï
        loginTime: new Date().toISOString()
      };
      
      try {
        uni.setStorageSync('login_info', loginInfo);
        
        // ÊèêÁ§∫ÁôªÂΩïÊàêÂäü
        uni.showToast({
          title: 'Ê∏∏ÂÆ¢ÁôªÂΩïÊàêÂäü',
          icon: 'success',
          duration: 1500
        });

        // Âª∂ËøüË∑≥ËΩ¨Âà∞È¶ñÈ°µ
        setTimeout(() => {
          this.enterApp();
        }, 1500);
        
      } catch (error) {
        console.error('Ê∏∏ÂÆ¢ÁôªÂΩïÂ§±Ë¥•', error);
        uni.showToast({
          title: 'ÁôªÂΩïÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none'
        });
      }
    },

    /**
     * ÂæÆ‰ø°ÊéàÊùÉÁôªÂΩï‰∏ªÊµÅÁ®ã
     * ÊµÅÁ®ãËØ¥ÊòéÔºö
     * 1. Ë∞ÉÁî® uni.getUserProfile Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØÔºàÊòµÁß∞„ÄÅÂ§¥ÂÉèÔºâ- ÂøÖÈ°ªÁî±Áî®Êà∑ÁÇπÂáªÁõ¥Êé•Ëß¶Âèë
     * 2. Ë∞ÉÁî® wx.login Ëé∑Âèñ‰∏¥Êó∂ÁôªÂΩïÂá≠ËØÅ code
     * 3. Â∞ùËØïË∞ÉÁî®ÂêéÁ´ØÁôªÂΩïAPIÔºåÂ¶ÇÊûúÂ§±Ë¥•Âàô‰ΩøÁî®Ê®°ÊãüÁôªÂΩï
     * 4. ÂâçÁ´Ø‰øùÂ≠òÁôªÂΩïÁä∂ÊÄÅÂíåÁî®Êà∑‰ø°ÊÅØ
     * 5. Áõ¥Êé•Ë∑≥ËΩ¨Âà∞È¶ñÈ°µ
     */
    async handleWxLogin() {
      this.isLoading = true;
      try {
        // 1. Ëé∑ÂèñÂæÆ‰ø°Áî®Êà∑Âü∫Á°ÄËµÑÊñô
        const userProfile = await uni.getUserProfile({
          desc: 'Áî®‰∫éÂÆåÂñÑÁî®Êà∑ËµÑÊñô'
        });
        
        // 2. Ëé∑ÂèñÂæÆ‰ø°ÁôªÂΩïcode
        const code = await this.getWxLoginCode();

        // 3. Â∞ùËØïË∞ÉÁî®ÂêéÁ´ØÁôªÂΩïAPIÔºåÂ¶ÇÊûúÂ§±Ë¥•Âàô‰ΩøÁî®Ê®°ÊãüÁôªÂΩï
        let loginResult;
        try {
          loginResult = await http.post(config.API.LOGIN.WECHAT, {
            code,
            nickName: userProfile.userInfo.nickName,
            avatarUrl: userProfile.userInfo.avatarUrl
          });
        } catch (apiError) {
          console.warn('ÂêéÁ´ØAPIË∞ÉÁî®Â§±Ë¥•Ôºå‰ΩøÁî®Ê®°ÊãüÁôªÂΩï', apiError);
          // ‰ΩøÁî®Ê®°ÊãüÁôªÂΩïÊï∞ÊçÆ
          loginResult = {
            token: 'mock_token_' + Date.now(),
            openid: 'mock_openid_' + Date.now(),
            session_key: 'mock_session_' + Date.now(),
            success: true
          };
          
          // ÊèêÁ§∫Áî®Êà∑‰ΩøÁî®Ê®°ÊãüÁôªÂΩï
          uni.showToast({
            title: 'ÂêéÁ´ØÊúçÂä°Êú™Â∞±Áª™Ôºå‰ΩøÁî®Ê®°ÊãüÁôªÂΩï',
            icon: 'none',
            duration: 2000
          });
        }

        // 4. ‰øùÂ≠òÁôªÂΩï‰ø°ÊÅØÂà∞Êú¨Âú∞
        // Â§ÑÁêÜÂêéÁ´ØÂìçÂ∫îÊ†ºÂºèÔºöÂèØËÉΩÊòØ {success: true, data: {token: ...}} Êàñ {token: ...}
        const responseData = loginResult.data || loginResult;
        const token = responseData.token || loginResult.token || '';
        const openid = responseData.openid || loginResult.openid || '';
        const sessionKey = responseData.session_key || loginResult.session_key || '';
        const isSuccess = loginResult.success !== false; // Â¶ÇÊûúÊ≤°ÊúâsuccessÂ≠óÊÆµÔºåÈªòËÆ§‰∏∫ÊàêÂäü
        
        const loginInfo = {
          isLoggedIn: true,
          token: token,
          openid: openid,
          sessionKey: sessionKey,
          userInfo: {
            nickName: userProfile.userInfo.nickName,
            avatarUrl: userProfile.userInfo.avatarUrl,
            displayName: userProfile.userInfo.nickName,
            displayAvatar: userProfile.userInfo.avatarUrl
          },
          loginTime: new Date().toISOString(),
          isMock: !isSuccess // Ê†áËÆ∞ÊòØÂê¶‰∏∫Ê®°ÊãüÁôªÂΩï
        };
        
        // Ë∞ÉËØï‰ø°ÊÅØÔºöÊ£ÄÊü•tokenÊòØÂê¶Ê≠£Á°ÆÊèêÂèñ
        if (process.env.NODE_ENV === 'development') {
          console.log('ÁôªÂΩïÂìçÂ∫îÊï∞ÊçÆ:', loginResult);
          console.log('ÊèêÂèñÁöÑToken:', token ? `Â∑≤ÊâæÂà∞ÔºåÈïøÂ∫¶: ${token.length}` : 'Êú™ÊâæÂà∞');
          if (!token) {
            console.error('‚ùå TokenÊèêÂèñÂ§±Ë¥•ÔºÅÂìçÂ∫îÁªìÊûÑ:', JSON.stringify(loginResult, null, 2));
          }
        }
        
        uni.setStorageSync('login_info', loginInfo);
        this.userInfo = loginInfo.userInfo;
        this.isLoggedIn = true;
        
        // È™åËØÅtokenÊòØÂê¶‰øùÂ≠òÊàêÂäü
        const savedLoginInfo = uni.getStorageSync('login_info');
        if (process.env.NODE_ENV === 'development') {
          console.log('‰øùÂ≠òÂêéÁöÑÁôªÂΩï‰ø°ÊÅØ:', savedLoginInfo);
          console.log('‰øùÂ≠òÂêéÁöÑToken:', savedLoginInfo?.token ? `‚úÖ Â∑≤‰øùÂ≠òÔºåÈïøÂ∫¶: ${savedLoginInfo.token.length}` : '‚ùå Êú™‰øùÂ≠ò');
        }
        
        // Â¶ÇÊûútoken‰∏∫Á©∫ÔºåÁªôÂá∫Ë≠¶Âëä
        if (!token || !token.trim()) {
          console.error('‚ö†Ô∏è Ë≠¶ÂëäÔºöToken‰∏∫Á©∫ÔºåÁôªÂΩïÂèØËÉΩÂ§±Ë¥•ÔºÅ');
          uni.showModal({
            title: 'ÁôªÂΩïË≠¶Âëä',
            content: 'Êú™Ëé∑ÂèñÂà∞ÊúâÊïàÁöÑÁôªÂΩïÂá≠ËØÅÔºåÈÉ®ÂàÜÂäüËÉΩÂèØËÉΩÊó†Ê≥ï‰ΩøÁî®„ÄÇËØ∑Ê£ÄÊü•ÂêéÁ´ØÊúçÂä°ÊòØÂê¶Ê≠£Â∏∏„ÄÇ',
            showCancel: false
          });
        }

        // 5. ÊèêÁ§∫ÁôªÂΩïÊàêÂäü
        uni.showToast({
          title: 'ÁôªÂΩïÊàêÂäü',
          icon: 'success',
          duration: 1500
        });

        // 6. Âª∂ËøüË∑≥ËΩ¨Âà∞È¶ñÈ°µÔºàÂ§¥ÂÉè‰øÆÊîπÂäüËÉΩÂ∞ÜÂú®‰∏™‰∫∫ËµÑÊñôÈ°µÈù¢Êèê‰æõÔºâ
        setTimeout(() => {
          this.enterApp();
        }, 1500);
        
      } catch (e) {
        console.error('ÂæÆ‰ø°ÁôªÂΩïÂ§±Ë¥•', e);
        uni.showToast({
          title: e.errMsg || 'ÁôªÂΩïÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none'
        });
      } finally {
        this.isLoading = false;
      }
    },



    /**
     * Ë∞ÉÁî®ÂæÆ‰ø° wx.login Êé•Âè£Ëé∑Âèñ‰∏¥Êó∂ÁôªÂΩïÂá≠ËØÅ code
     * @returns {Promise<string>} ËøîÂõû codeÂ≠óÁ¨¶‰∏≤
     */
    getWxLoginCode() {
      return new Promise((resolve, reject) => {
        // #ifdef MP-WEIXIN
        uni.login({
          provider: 'weixin',
          success: (res) => {
            if (res.code) {
              resolve(res.code);
            } else {
              reject(new Error('Ëé∑ÂèñcodeÂ§±Ë¥•'));
            }
          },
          fail: (err) => {
            reject(err);
          }
        });
        // #endif

        // #ifndef MP-WEIXIN
        // H5 ÊàñÂÖ∂‰ªñÂπ≥Âè∞ËøîÂõûÊ®°Êãü code
        resolve('mock_code_' + Date.now());
        // #endif
      });
    },

    /**
     * Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØÔºàÂæÆ‰ø°Â∞èÁ®ãÂ∫èÔºâ
     * @returns {Promise<Object>} ËøîÂõûÁî®Êà∑‰ø°ÊÅØÂØπË±°
     */
    getUserProfile() {
      return new Promise((resolve, reject) => {
        // #ifdef MP-WEIXIN
        uni.getUserProfile({
          desc: 'Áî®‰∫éÂÆåÂñÑÁî®Êà∑ËµÑÊñô',
          success: (res) => {
            resolve(res.userInfo);
          },
          fail: (err) => {
            reject(err);
          }
        });
        // #endif

        // #ifndef MP-WEIXIN
        // H5 ÊàñÂÖ∂‰ªñÂπ≥Âè∞‰ΩøÁî®Ê®°ÊãüÊï∞ÊçÆ
        resolve({
          nickName: 'Ê∏∏ÂÆ¢Áî®Êà∑',
          avatarUrl: '/static/zhuye/smile.png'
        });
        // #endif
      });
    },

    /**
     * Â∞ÜÁôªÂΩï‰ø°ÊÅØÂèëÈÄÅÂà∞ÂêéÁ´ØÊúçÂä°Âô®Ôºà‰ΩøÁî®Â∞ÅË£ÖÂ•ΩÁöÑAPIÔºâ
     * @param {string} code - ÂæÆ‰ø°ÁôªÂΩïÂá≠ËØÅ
     * @param {Object} userInfo - Áî®Êà∑‰ø°ÊÅØ
     * @returns {Promise<Object>} ËøîÂõûÂêéÁ´ØÂìçÂ∫îÊï∞ÊçÆ
     */
    async sendLoginToBackend(code, userInfo) {
      try {
        // Ë∞ÉÁî®Â∞ÅË£ÖÂ•ΩÁöÑÁôªÂΩïAPI
        const result = await wxLogin(code, userInfo);
        return result;
      } catch (error) {
        // Áõ¥Êé•ÊäõÂá∫ÈîôËØØÔºå‰∏çËøîÂõûÊ®°ÊãüÊï∞ÊçÆ
        throw error;
      }
    },

    // ËøõÂÖ•Â∫îÁî®
    enterApp() {
      uni.reLaunch({
        url: '/pages/index/index'
      });
    },


  }
};
</script>

